import {
  BasicCalculator
} from '../../core/o-calculator-base';

export class ExtendedTouchpointCalculator extends BasicCalculator {

  init() {}
  constructor() {
    super();
  }

  credit(type, money) {
    let result = 0;
    switch (type) {
      case "1-Credit":
        result = this.multiply(0.5, money);
        break;
      case "2-Credit":
        result = this.multiply(0.75, money);
        break;
      case "3-Credit":
        result = +money;
        break;
      case "4-Credit":
        result = this.multiply(1.5, money);
        break;
      case "5-Credit":
        result = this.multiply(2, money);
        break;
      case "6-Credit":
        result = this.multiply(1.5, money);
        break;
      case "7-Credit":
        result = this.multiply(4, money);
        break;
    }
    return result;
  }

  debit(type, moneyInside, moneyOutside) {
    if (moneyInside == "")
      moneyInside = 0;
    if (moneyOutside == "")
      moneyOutside = 0;

    let result = 0;
    switch (type) {
      case "1-Debit":
        result = this.add(this.divide(moneyInside, 4), this.divide(moneyOutside, 2));
        break;
      case "2-Debit":
        result = this.add(this.multiply(this.divide(moneyInside, 4), 1.25), this.multiply(this.divide(moneyOutside, 2), 1.25));
        break;
      case "3-Debit":
        result = this.add(this.multiply(this.divide(moneyInside, 4), 1.5), this.multiply(this.divide(moneyOutside, 2), 1.5));
        break;
      case "4-Debit":
        result = this.add(this.multiply(this.divide(moneyInside, 4), 1.25), this.multiply(this.divide(moneyOutside, 2), 1.25));
        break;
      case "5-Debit":
        result = this.add(this.multiply(this.divide(moneyInside, 4), 1.5), this.multiply(this.divide(moneyOutside, 2), 1.5));
        break;
      case "6-Debit":
        result = this.add(this.multiply(this.divide(moneyInside, 2), 1.5), this.multiply(this.divide(moneyOutside, 2), 1.5));
        break;
      case "7-Debit":
        result = this.add(this.divide(moneyInside, 4), this.divide(moneyOutside, 2));
        break;
    }
    return result;
  }

  tenures(time, money) {
    let min = 100000;
    let step = 10000;
    let result = 0;
    let cap = 50000;

    if (money >= min) {
      switch (time) {
        case "1-Fixed":
          result = this.divide(this.multiply(10, money), step);
          break;
        case "2-Fixed":
          result = this.divide(this.multiply(20, money), step);
          break;
        case "3-Fixed":
          result = this.divide(this.multiply(40, money), step);
          break;
        case "4-Fixed":
          result = this.divide(this.multiply(100, money), step);
          break;
        case "5-Fixed":
          result = this.divide(this.multiply(300, money), step);
          break;
      }
    }
    if (result > cap) {
      return cap;
    } else {
      return result;
    }
  }

  insurance(type, money) {
    let cap = 400000;
    let step = 1000;
    let min = 10000;
    let result = 0;

    if (money >= min) {
      switch (type) {
        case "1-Insurance":
        case "4-Insurance":
          result = this.multiply(this.divide(money, step), 2000) //Regular Premium-Savings Plans or Regular Contribution-Takaful Life Plans
          break;
        case "2-Insurance":
        case "5-Insurance":
          result = this.multiply(this.divide(money, step), 750) //Regular Premium-Protection Plans or Regular Contribution-Takaful Savings Plans
          break;
        case "3-Insurance":
        case "6-Insurance":
          result = this.multiply(this.divide(money, step), 250) //Single Premium or Single Contribution
          break;
      }
    }
    if (result > cap)
      return cap;
    else
      return result;
  }
  /** Current And Savings**/
  savingsAndCurrent(money, type) {
    let step = 1000;
    let min = 10000;
    let min2 = 50000
    let cap = 200000;
    let cap2 = 300000;
    let result = 0;
    switch (type) {
      case "1-Savings":
        if (money >= min) {
          if (money < 25000)
            result = this.multiply(this.divide(money, step), 15)
          else if (money < 99999)
            result = this.multiply(this.divide(money, step), 20)
          else
            result = this.multiply(this.divide(money, step), 25)
        }
        if (result > cap)
          result = cap;
        break;
      case "2-Savings":
        if (money >= min2)
          result = this.multiply(this.divide(money, step), 25)
        if (result > cap2)
          result = cap2;
        break;
    }
    return result;
  }

  /** LOANS **/
  personalLoan(money) {
    let step = 1000;
    let min = 10000;
    let cap = 75000;
    let result = 0;

    if (money >= min) {
      if (money < 150000)
        result = this.multiply(this.divide(money, step), 200)
      else
        result = this.multiply(this.divide(money, step), 300)
    }
    if (result > cap) {
      return cap
    } else {
      return result;
    }
  }

  mortgageLoan(money) {
    let step = 1000;
    let min = 250000;
    let cap = 500000;
    let result = 0;

    if (money >= min) {
      result = this.multiply(this.divide(money, step), 200)
    }
    if (result > cap) {
      return cap
    } else {
      return result;
    }
  }

  carLoan(money) {
    let step = 1000;
    let min = 10000;
    let result = 0;

    if (money >= min) {
      result = this.multiply(this.divide(money, step), 50)
    }
    return result;
  }

  /** Investment **/
  investment(money) {
    let min = 18000;
    let step = 1000;
    let cap = 150000;
    let result = 0;

    if (money >= min) {
      if (money - min >= step){
        result = this.multiply(this.divide(this.subtract(money, min), step), 85)
      }
      result = this.add(result, 1500);
    }
    else {
      result = 0;
    }
    if (result > cap)
      return cap;
    else
      return result;
  }

  electronic(bills) {
    let result = 0;
    let cap = 12000;
    result = bills * 600;

    if (result > cap)
      return cap;
    else
      return result;
  }
}
