import {
  BasicCalculator
} from '../../core/o-calculator-base';

export class ActiveSaver extends BasicCalculator {
  constructor(elemnt) {
    super();
    this.$calculator = elemnt;
    this.$money = this.$calculator.find('.money');
    this.$moneyMin = this.$calculator.find('.money').parent().next('.slider').attr("min");
    this.$moneyMax = this.$calculator.find('.money').parent().next('.slider').attr("max");
    this.$interest = this.$calculator.find('.o-calculator-radio');
    this.$output = this.$calculator.find('.c-calculator__value');
    this.$button = this.$calculator.find('.c-calculator__button');
  }
  init() {
    this.addEventListeners();
    this.calculate();
  }
  addEventListeners() {
    this.$money.on('change', e => {
        if (parseInt(this.$money.val()) < this.$moneyMin) {
          this.$money.val(this.$moneyMin);
        }
    });
    this.$money.on('propertyChange', e => {
      if (parseInt(this.$money.val()) > this.$moneyMax) {
        this.$money.val(this.$moneyMax);
      }
      this.calculate()
    })
    this.$interest.on('change', e => {
      this.calculate()
    })
  }

  calculate() {
    let time;
    let money = this.$money[0].value;
    while (money.includes(','))
      money = money.replace(',', '')

    this.$button.addClass('active')

    this.$interest.find('input').each((i, e) => {
      if (e.checked)
        time = e.value
    })
    switch (time) {
      case "1":
        if (money < 10000)
          this.setMillionsMask(Math.round(money), this.$output)
        else
          this.once(parseInt(money));
        break;
      case "2":
        this.monthly(parseInt(money));
        break;

    };
  }
  once(money) {
    for (let i = 0; i < 12; i++) {
      if (money > 1999999)
        money = this.add(money, this.multiply(this.divide(this.multiply(money, 0.020), 360), 30))
      else if (money > 499999)
        money = this.add(money, this.multiply(this.divide(this.multiply(money, 0.018), 360), 30))
      else
        money = this.add(money, this.multiply(this.divide(this.multiply(money, 0.017), 360), 30))
    }
    this.setMillionsMask(Math.round(money), this.$output)
  }

  monthly(money) {
    let deposit = money;
    if (money > 1999999)
      money = this.add(money, this.multiply(this.divide(this.multiply(money, 0.020), 360), 30))
    else if (money > 499999)
      money = this.add(money, this.multiply(this.divide(this.multiply(money, 0.018), 360), 30))
    else if (money > 9999)
      money = this.add(money, this.multiply(this.divide(this.multiply(money, 0.017), 360), 30))
    for (let i = 0; i < 11; i++) {
      money = money + deposit;
      if (money > 1999999)
        money = this.add(money, this.multiply(this.divide(this.multiply(money, 0.020), 360), 30))
      else if (money > 499999)
        money = this.add(money, this.multiply(this.divide(this.multiply(money, 0.018), 360), 30))
      else if (money > 9999)
        money = this.add(money, this.multiply(this.divide(this.multiply(money, 0.017), 360), 30))
    }
    this.setMillionsMask(Math.round(money), this.$output)
  }
}
