import { initCoreModule } from '../core/toolkit';
import {
  initializeClass
} from '../core/helpers.js';
/* Calculator Imports */
import { ExtendedTouchpointCalculator } from './calculators/o-calculator-extended-touchpoint.js';
import { AmountTimeInterest } from './calculators/amount-time-interest.js';
import { ActiveSaver } from './calculators/active-saver.js';
import { HomeSaver } from './calculators/home-saver.js'
import { TouchPoints } from './calculators/touchpoints.js'
import { BarChart } from './calculators/c-bar-chart.js'

initCoreModule();
$(document).ready(e => {
  /* Calculators Initialize */
  initializeClass('.js-amount-time-interest', AmountTimeInterest);

  initializeClass('.js-active-saver', ActiveSaver);

  initializeClass( '.js-home-saver', HomeSaver);

  initializeClass( '.js-touchpoints', TouchPoints);
  
  initializeClass('.js-bar-chart', BarChart);
  
  initializeClass('.js-touchpoint-calculator', ExtendedTouchpointCalculator);
})