import * as d3 from "d3";
import {
  breakpoint
} from '../../core/helpers.js';

export class BarChart {
  constructor($selector) {
    this.id = '#' + $selector[0].id;
    this.divNode = d3.select("body").node();
    this.color = ['#ec0100', '#ba1b19', '#751515', '#510c0b']
    this.$money = $('.o-range-slider__value.money');
    this.$time = $('.months');
    this.$input = $('.c-calculator__input').find('input');
  }

  init() {
      this.barGraph();
      this.addEventListeners();
  }
  addEventListeners() {
    this.$money.on('propertyChange', e => {
      //console.log($(e.target).parents('.o-range-slider').find('.slider').attr('min'));
      //.val());
      //if (parseInt($(e.target).val()) > parseInt($(e.target).parents('.o-range-slider').find('.slider').attr('min'))) {
      if (parseInt($(e.target).val()) > 0) {
        if ((this.replaceComas($('#total_loan_payment').find('td')[1].innerText) != "0") && $('#tenure').find('td')[1].innerText != "")
          this.barGraph()
      }
    });
    this.$time.on('propertyChange', e => {
        if ((this.replaceComas($('#total_loan_payment').find('td')[1].innerText) != "0") && $('#tenure').find('td')[1].innerText != "")
          this.barGraph()
    });
    this.$input.on('change', e => {
      if (parseInt($(e.target).val()) > 0) {
        if ((this.replaceComas($('#total_loan_payment').find('td')[1].innerText) != "0") && $('#tenure').find('td')[1].innerText != "")
          this.barGraph()
      }
    });

    $(window)
      .on('resize', e => {
        this.barGraph();
      });
  }


  barGraph() {
    try {
      //home saver data first and then standard
      var data = [{
        "title": "Total interest cost",
        "value": 2975
      }, {
        "title": "Total interest cost",
        "value": 36657
      }]; //placeholder
      var tenuredata = [{
        "index": 1,
        "title": "Tenure term",
        "value": 22
      }, {
        "index": 0,
        "title": "Tenure term",
        "value": 25
      }]; //placeholder

      //we populate the data
      data[0].value = this.replaceComas($('#total_loan_payment').find('td')[2].innerText)
      data[1].value = this.replaceComas($('#total_loan_payment').find('td')[1].innerText)
      if (parseInt($('#tenure').find('td')[1].innerText) != parseInt($('#effective_savings_tenor')[0].innerText / 12))
        tenuredata[0].value = (parseInt($('#tenure').find('td')[1].innerText) - parseInt($('#effective_savings_tenor')[0].innerText / 12))
      else
        tenuredata[0].value = $('#tenure').find('td')[1].innerText
        tenuredata[1].value = $('#tenure').find('td')[1].innerText

      var fullgraphdata = $('.c-calculator__graph-details-value span');
      var standardhomeyears = fullgraphdata[2].innerText;
      var homesaverhomeyears = fullgraphdata[3].innerText;
      var rationhomeyears = 0; 
      var rationhome = 0;

      if (standardhomeyears == 0) {
        standardhomeyears = 1
      }
      rationhomeyears = parseInt((homesaverhomeyears * 100) / standardhomeyears);
      
      var standardhome = this.replaceComas(fullgraphdata[0].innerText);
      var homesaverhome = this.replaceComas(fullgraphdata[1].innerText);
      if (standardhome == 0) {
        standardhome = 1
      }
      rationhome = parseInt((homesaverhome * 100) / standardhome);
      var monthtitle = $('.c-calculator__mname')[0].innerText;

      d3.selectAll(".c-calculator__graph-item svg").remove(); //we clear the possible existing data for future updates
      d3.selectAll(".c-calculator__graph-item text").remove(); //we clear the possible existing data for future updates

      // set the dimensions and margins of the graph
      var margin = {
          top: 20,
          right: 20,
          bottom: 30,
          left: 40
        },
      marginSmall = {
          top: 20,
          right: 48,
          bottom: 30,
          left: 48
        },
      marginXSmall = {
          top: 20,
          right: 24,
          bottom: 30,
          left: 24
        },
        width = 600 - margin.left - margin.right,
        height = 90 - margin.top - margin.bottom;
      var rationhomewidth = (rationhome * width) / 100 + 30 ;
      var rationhomeyearswidth = (rationhomeyears * width) / 100 + 30;
      var classmobile = "";
      if (breakpoint() === 'xs' || breakpoint() === 'sm') {
        width = $(window).innerWidth() - marginSmall.right - marginSmall.left;
        rationhomewidth = (rationhome * width) / 100 - 35;
        rationhomeyearswidth = (rationhomeyears * width) / 100 - 60;
        classmobile = "mobile";
      }
      if (breakpoint() === 'xs') {
        width = $(window).innerWidth() - marginXSmall.right - marginXSmall.left;
      }
      // set the ranges
      var y = d3.scaleBand()
        .range([height, 0])
        .padding(0.1);

      var x = d3.scaleLinear()
        .range([0, width]);

      var paddingleftValue = (breakpoint() === 'xs' || breakpoint() === 'sm') ? '0' : '40px';
      var paddingleftFirstValue = (breakpoint() === 'xs' || breakpoint() === 'sm') ? '0' : '35px';

      // append the svg object to the body of the page
      d3.select("#standard" + classmobile).append("text")
        .text("0%")
        .style("padding-left", paddingleftFirstValue)
        .append("text")
        .text("100%")
        .style("padding-left", (width - 40) + "px");


      var svg = d3.select("#standard" + classmobile).append("svg")
        //.style("border-right", (breakpoint() === 'xs' || breakpoint === 'sm') ? "0" : "1px solid #e9eaeb")
        .attr("class",  "graph-holder")
        .style("margin", "0 " + paddingleftValue + " auto")
        .attr("width", width)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform", "translate(0," + margin.top + ")");

      // Scale the range of the data in the domains
      // x.domain([0, d3.max(data, function(d) {
      //   return d.value;
      // })])
      y.domain(data.map(function(d) {
        return d.value;
      }));

      // append the rectangles for the bar chart
      svg.selectAll(".bar")
        .data(data)
        .enter().append("rect")
        .attr("class", "bar")
        .attr("width", function(data, i) {
          if (standardhome == 0) {standardhome = 1;}
          rationhome = (homesaverhome/standardhome) * 100;
          if(rationhome <= 100) {
            rationhome = parseInt(rationhome);
            if(i == 0) {
              return parseInt(rationhome * width/100);
            }
          }else{
            if(i == 1) {
              if (rationhome == 0) {
                rationhome = 1;
              }
              return parseInt((100/rationhome) * width) - 10;
            }
          }
          return width;
        })
        .attr("height", "30px")

      d3.select("#saver" + classmobile).append("text")
        .text("0")
        .style("padding-left", paddingleftFirstValue)
        .append("text")
        .text(standardhomeyears + "\u00A0" + monthtitle)
        .style("padding-left", (width - 70) + "px");

      // var svgsaver = d3.select("#saver" + classmobile).append("svg")
      //   .style("border-left", "1px solid #000000")
      //   .style("border-right", (breakpoint() === 'xs' || breakpoint === 'sm') ? "0px" : "1px solid #e9eaeb")
      //   .style("margin-left", paddingleftValue)
      //   .attr("width", width)
      //   .attr("height", height + margin.top + margin.bottom)
      //   .append("g")
      //   .attr("transform", "translate(0," + margin.top + ")");

      var svgsaver = d3.select("#saver" + classmobile).append("svg")
      .attr("class", "graph-holder")
      .style("margin-left", paddingleftValue)
      .attr("width", width)
      .attr("height", height + margin.top + margin.bottom)
      .append("g")
      .attr("transform", "translate(0," + margin.top + ")");

      y.domain(tenuredata.map(function(d) {
        return d.value;
      }));

      // append the rectangles for the bar chart
      svgsaver.selectAll(".bar")
        .data(tenuredata)
        .enter().append("rect")
        .attr("class", "bar")
        .attr("width", function(data, i) {
          if(i == 0) {
            if (standardhomeyears == 0) {standardhomeyears = 1;}
            return (homesaverhomeyears * width)/ standardhomeyears;
          }
          return width;
        })
        .attr("height", "30px");

        if(rationhome > 100) {
          rationhome = parseInt((100/rationhome)*100)
        }

      d3.select("#standard" + classmobile).append("text")
        .text(rationhome + "%")
        .style("padding-left", rationhomewidth + "px");

      d3.select("#saver" + classmobile).append("text")
        .text(homesaverhomeyears + "\u00A0" + monthtitle)
        .style("padding-left", rationhomeyearswidth + "px");
    }
    catch (e) {
      //ignore
    }
  }

  replaceComas(value) {
    while (value.includes(','))
      value = value.replace(',', '')
    return value;
  }
}
