import {
  BasicCalculator
} from '../../core/o-calculator-base';
export class HomeSaver extends BasicCalculator {
  constructor(elemnt) {
    super();
    this.$calculator = elemnt;
    this.$money = this.$calculator.find('.money');
    this.$time = this.$calculator.find('.months');
    this.$input = this.$calculator.find('.c-calculator__input').find('input');
    this.$output = this.$calculator.find('.c-calculator__value');
    this.$outputMonths = this.$calculator.find('.c-calculator__months');
    this.$homeSaverTable = this.$calculator.find('.js-home-saver-table');
    this.$balanceAmount = parseInt(this.$input.data("startBalance"));
    this.$input.val(this.$balanceAmount).attr("placeholder", this.$balanceAmount);
    this.$showComparison = this.$calculator.find('.show-calc-info');
    /**         Table outputs        */
    //Loan Amount (AED)
    this.$loanAmount = this.$homeSaverTable.find('#loan__amount').find('td');
    //Original Loan Tenure (Years)
    this.$tenure = this.$homeSaverTable.find('#tenure').find('td');
    //Average Account Balance Maintained (AED)
    this.$balanceMantained = this.$homeSaverTable.find('#average_maintained');
    //Regular Monthly Installment
    this.$emi = this.$homeSaverTable.find('#emi').find('td');
    //Actual Installments required to be Paid
    this.$installments = this.$homeSaverTable.find('#installments_required').find('td');
    //Total Payment towards Loan
    this.$total_loan_payment = this.$homeSaverTable.find('#total_loan_payment').find('td');
    //Total Interest paid towards Loan
    this.$interest_paid = this.$homeSaverTable.find('#interest_paid').find('td');
    //Effective Savings in Interest Cost (Even on Higher Loan Rate)
    this.$effective_savings_even = this.$homeSaverTable.find('#effective_savings_even')[0];
    //Effective Savings in Interest Cost (In % as compared to Standard Home Loan)
    this.$effective_savings_compared = this.$homeSaverTable.find('#effective_savings_compared')[0];
    //Effective Savings in Tenor (In Months) - How early you Own the home
    this.$effective_savings_tenor = this.$homeSaverTable.find('#effective_savings_tenor')[0];
    this.$home_interest = this.$homeSaverTable.find('#home_interest')[0];
    this.$saver_interest = this.$homeSaverTable.find('#saver_interest')[0];
    this.$interest_rate_monthly = this.$homeSaverTable.find('#interest_rate_monthly')[0];

    this.$buttonClear = this.$calculator.find('.js-clear');

    this.$graph_details_result_hvalues = this.$calculator.find('.c-calculator__graph-details-value span');
  }

  init() {
    try {
      this.addEventListeners();
      this.calculate();
    }
    catch (e) {
      alert(e);
    }
  }
  addEventListeners() {    
    this.$money.on('propertyChange', e => {
      if (parseInt($(e.target).val()) > parseInt($(e.target).parents('.o-range-slider').find('.slider').attr('min'))) {
      //if (parseInt($(e.target).val()) > 0) {
        this.calculate();
      }
    });
    this.$time.on('propertyChange', e => {
      if (parseInt($(e.target).val()) > 0) {
        this.calculate();
      }
    });
    this.$input.on('change', e => {
      this.calculate();
    });
    this.$showComparison.on('click', e => {
      this.$calculator.addClass('-js-show-calculations');
    });

   this.$buttonClear.on('click', e => {
     this.$calculator.removeClass('-js-show-calculations');
     $("html, body").animate({
        scrollTop: $('.js-home-saver').offset().top - 100
      }, 1000, 'swing');

      this.reset();
    })
  }

 reset(){
    // alert($('.slider')[1].min);
    let moneyStartValue = $('.slider')[0].getAttribute('data-start-value') ? $('.slider')[0].getAttribute('data-start-value') : $('.slider')[0].min;
    let timeStartValue = $('.slider')[1].getAttribute('data-start-value') ? $('.slider')[1].getAttribute('data-start-value') : $('.slider')[1].min;
    $(".o-range-slider__value:eq(0)").attr("value", moneyStartValue).val(moneyStartValue);
    $(".o-range-slider__value:eq(1)").attr("value", timeStartValue).val(timeStartValue);
    $(".o-range-slider__value:eq(0)").trigger('change', moneyStartValue);
    $(".o-range-slider__value:eq(1)").trigger('change', timeStartValue);
    //$('.slider').parent().trigger("change");
    this.$balanceAmount = parseInt(this.$input.data("startBalance"));
    this.$input.val(this.$balanceAmount).attr("placeholder", this.$balanceAmount);
    this.calculate();
  }

  calculate() {
    let money,time,balanceAccount,home_interest,saver_interest,homeEmi,saverEmi,saverRate,installment_home,installment_saver,
    total_loan_payment_home, total_loan_payment_saver;

    home_interest = this.interest(this.$home_interest.innerText.split('%')[0]);
    saver_interest = this.interest(this.$saver_interest.innerText.split('%')[0]);
    saverRate = this.divide(this.divide(saver_interest,100),12);
    money = this.replaceComas(this.$money[0].value);
    time = this.$time[0].value;
    //Average Account Balance Maintained (AED) if we dont have any actual input we will use the placeholder
    balanceAccount = this.$input.val() ? this.$input.val() : this.$balanceAmount;

    this.loanTimeBalanceOutput(money,time,balanceAccount);

    //Regular Monthly Installment
    homeEmi = this.reducedBalanceInterest(home_interest, this.multiply(time,12), money);
    saverEmi = this.reducedBalanceInterest(saver_interest, this.multiply(time,12), money);

    //Actual Installments required to be Paid
    installment_home = this.multiply(parseInt(time),12)
    installment_saver = this.saverinstallments(parseFloat(money),parseFloat(saverEmi.toFixed(2)),saverRate.toFixed(9),balanceAccount);

     //we need to round it up on outputs but we need all digits for logic calcs
    this.installmentsOutput(Math.round(homeEmi),Math.round(saverEmi),installment_home,installment_saver);

    //Total Payment towards Loan
    total_loan_payment_home = Math.round(this.multiply(homeEmi, this.multiply(time, 12)));
    total_loan_payment_saver = Math.round(this.multiply(saverEmi,installment_saver));
    this.setMillionsMask(total_loan_payment_home, this.$total_loan_payment[1]);
    this.setMillionsMask(total_loan_payment_saver, this.$total_loan_payment[2]);

    //Displaying Amount For Total Payment Loans
    let standandloancostsection = this.$graph_details_result_hvalues[0];
    let homesaverloancostsection = this.$graph_details_result_hvalues[1];
    this.setMillionsMask(total_loan_payment_home, standandloancostsection);
    this.setMillionsMask(total_loan_payment_saver, homesaverloancostsection);


    //Interests Output
    this.interestOutput(money,saverEmi,installment_saver,total_loan_payment_home,total_loan_payment_saver);

    this.$effective_savings_tenor.innerText = this.subtract(installment_home,installment_saver)

    this.$outputMonths[0].innerText = this.subtract(installment_home,installment_saver);
    this.$outputMonths[2].innerText = this.$outputMonths[0].innerText;

    if (parseInt(this.$effective_savings_even.innerText)>0){
      this.$outputMonths[1].innerText = this.subtract(installment_home,installment_saver)
      $('.message1').removeClass('hidden')
      $('.message2').addClass('hidden')
      $('.message3').addClass('hidden')
    }else{
      if (this.subtract(installment_home,installment_saver)>12){
        $('.message2').removeClass('hidden')
        $('.message1').addClass('hidden')
        $('.message3').addClass('hidden')
      }else{
        $('.message1').addClass('hidden')
        $('.message2').addClass('hidden')
        $('.message3').removeClass('hidden')
      }
    }
  }

  loanTimeBalanceOutput(money,time,balanceAccount){
    //Loan Amount (AED)
    this.setMillionsMask(money, this.$loanAmount[1]);
    this.setMillionsMask(money, this.$loanAmount[2]);
    //Original Loan Tenure (Years)
    this.setMillionsMask(time, this.$tenure[1])
    this.setMillionsMask(time, this.$tenure[2])
    //Average Account Balance Maintained (AED)
    this.setMillionsMask(balanceAccount, this.$balanceMantained);
  }

  installmentsOutput(homeEmi,saverEmi,installment_home,installment_saver){
    //Regular Monthly Installment
    this.setMillionsMask(homeEmi, this.$emi[1]);
    this.setMillionsMask(saverEmi, this.$emi[2]);
    //Actual Installments required to be Paid
    this.setMillionsMask(installment_home,this.$installments[1]);
    this.setMillionsMask(installment_saver,this.$installments[2]);

    //Displaying Actual Installments required to be Paid for the Graph Details
    let standandloansection = this.$graph_details_result_hvalues[2];
    let homesaverloansection = this.$graph_details_result_hvalues[3];
    this.setMillionsMask(installment_home, standandloansection);
    this.setMillionsMask(installment_saver, homesaverloansection);
  }

  interest(value) {
    let loop;
    value = value.split('.')
    loop = value[1].length;
    for (var i = 0; i < loop; i++)
      value[1] = this.divide(value[1],10);
    return this.add(parseInt(value[0]),value[1]);
  }

  saverinstallments(total, monthly, rate, accountBalance) {
    let monthlyRate,interestComponent,principalComponent,returnOnCasa,months;
    monthlyRate = this.$interest_rate_monthly.innerText.split('%')[0];
    interestComponent = Math.round(this.multiply(total,rate));
    principalComponent = Math.round(this.subtract(monthly,interestComponent));
    monthlyRate = this.divide(this.divide(this.interest(monthlyRate),100),12).toFixed(9);//the bank calcs only take last 9 digits
    returnOnCasa = Math.round(this.returnOnCasa(monthlyRate, total, accountBalance));

    for (months = 0; total > monthly; months++) {
      returnOnCasa = Math.round(this.returnOnCasa(monthlyRate, total, accountBalance));
      interestComponent = Math.round(this.multiply(total,rate));
      principalComponent = Math.round(this.subtract(monthly,interestComponent));
      total = this.subtract(total,returnOnCasa,principalComponent);
    }
    return this.add(months,1);//we need to add the first month to the sum of the rest of the months
  }

  returnOnCasa(rate, total, accountBalance) {
    if (accountBalance < total)
      return this.multiply(accountBalance, rate)
    else
      return this.multiply(total, rate);
  }

  replaceComas(value) {
    while (value.includes(','))
      value = value.replace(',', '')
    return value;
  }

  interestOutput(money,saverEmi,installment_saver,total_loan_payment_home,total_loan_payment_saver){
    let interest_paid_home,interest_paid_saver,effective_savings;

    //Total Interest paid towards Loan
    interest_paid_home = this.subtract(total_loan_payment_home, money);
    this.setMillionsMask(interest_paid_home, this.$interest_paid[1]);
    interest_paid_saver = this.subtract(Math.round(this.multiply(saverEmi,installment_saver)),money);
    this.setMillionsMask(interest_paid_saver, this.$interest_paid[2]);

    //Effective Savings in Interest Cost (Even on Higher Loan Rate)
    effective_savings = this.subtract(total_loan_payment_home,total_loan_payment_saver);
    this.setMillionsMask(effective_savings, this.$effective_savings_even)

    this.setMillionsMask(effective_savings, this.$output)//Calculator output

    //Effective Savings in Interest Cost (In % as compared to Standard Home Loan)
    this.$effective_savings_compared.innerText = this.multiply(this.divide(effective_savings,interest_paid_home),100).toFixed(2) + '%';
  }

}
