  savingEvents() {
    let money = this.$saving.Overlay.find('.c-calculator__input-text-box');
    let output = this.$saving.Overlay.find('.c-calculator__value')[0];
    let option = this.$saving.Overlay.find('.o-calculator-radio');
    let done = this.$saving.Overlay.find('.o-btn');
    let result = output.innerHTML;
    let type = 0;
    let value = money[0].value;
    let errorText1 = $('.c-calculator__input-error.error_text1');
    let errorText2 = $('.c-calculator__input-error.error_text2');
    errorText1.css({'display': 'block'});
    errorText2.css({'display': 'none'});
    if (value == "") {
      this.uncheck(option.find('input'))
    }
    money.off().on('keydown', event => {
      this.checkInput(money.val().length, event);
      this.checkIsNumber(event);
    }).on('keyup', event => {
      this.blockInputToMax(money.val().length, event);
    })
    .on('input', e => {
      value = e.target.value;
      result = this.savingsAndCurrent(value, type).toFixed(2);
      this.setMillionsMask(result, output)
      if (result == 0)
        this.$saving.Overlay.find('.c-calculator-overlay__input').addClass('error')
      else
        this.$saving.Overlay.find('.c-calculator-overlay__input').removeClass('error')
    });
    option.off().on('change', e => {
      type = e.target.value;
      result = this.savingsAndCurrent(value, type).toFixed(2);
      this.setMillionsMask(result, output)
      if (type == '2-Savings') {
        errorText1.css({'display': 'none'});
        errorText2.css({'display': 'block'});
      }
      else {
        errorText1.css({'display': 'block'});
        errorText2.css({'display': 'none'});
      }
      if (result == 0)
        this.$saving.Overlay.find('.c-calculator-overlay__input').addClass('error')
      else
        this.$saving.Overlay.find('.c-calculator-overlay__input').removeClass('error')
    });
    done.off().on('click', e => {
      e.preventDefault();
      if (result == 0) {
        this.$saving.Overlay.find('.c-calculator-overlay__input').addClass('error')
      } else {
        this.$saving.Overlay.find('.c-calculator-overlay__input').removeClass('error')
        result = parseFloat(this.replaceCommas(output.innerHTML)).toFixed(2);
        this.activeButtons(this.$saving, result);
      }
    });
  }

